
import React, { useEffect, useState } from "react";
import gsap from "gsap";

function Footer() {
  const [currentMessageIndex, setCurrentMessageIndex] = useState(0);
  const [currentMessage, setCurrentMessage] = useState("");

  const messages = [
    "Agendate con la Cmara de Comercio y consulta los beneficios de estar formalizado en www.camarapamplona.org.co",
    "Recuerda nuestro horario de atencin     Lunes a Viernes    de 8:00am - 12:00pm    y    de 2:00pm - 6:00pm",
    "Nuestros asesores estan aqu para brindarte la informacin que necesitas",
    "La generacin de oportunidades de emprendimiento, fortalecimiento y aceleracin empresarial de la regin, son nuestro compromiso",
    "Siguenos en nuestras redes sociales Facebook, Instagram y Twitter como # Camara de Comercio de Pamplona",
    "Prestador de Servicios Turisticios-Alojamientos, no olvides alimentar da a da tu Tarjeta de Registro de Alojamiento",
    "Recuerda nuestros canales de contacto: ccpamplona@camarapamplona.org.co, telfonos (60)75680993 - 75684696 - 75682047 y WhatsApp: 333 033 3569",
    "Recuerda que el trmino de ley para atender tus tramites es de QUINCE (15) DAS HBILES",
    "No olvides calificar el servicio prestado por nuestro personal, pregunta con nuestras Asesoras como hacerlo",
    "Adems de tu registro en Cmara de Comercio, adquieres responsabilidades tributarias en la DIAN y en la Alcalda Municipal  (Impuesto de Industria y Comercio).",
    "Si tienes actividades de alto impacto, debes gestionar los permisos necesarios ante las autoridades locales (Planeacin municipal, sayco y acinpro, bomberos, entre otros).",
    "Recuerda que puedes realizar tu matrcula mercantil mediante la Ventanilla Unica Empresarial VUE, aplica solo para Pamplona."
  ];

  useEffect(() => {
    const mensajeElement = document.querySelector(".message-text");
    const footerContainer = document.querySelector(".mensajes");
    const mensajeWidth = mensajeElement.offsetWidth;
    const containerWidth = footerContainer.offsetWidth;

    const updateMessage = () => {
      const message = messages[currentMessageIndex];
      setCurrentMessage(message);

      gsap.set(mensajeElement, { x: containerWidth + mensajeWidth });
      gsap.to(mensajeElement, {
        x: -mensajeWidth,
        duration: containerWidth * 0.04,
        ease: "linear",
        onComplete: () => {
          setCurrentMessageIndex((currentMessageIndex + 1) % messages.length);
        },
      });
    };

    updateMessage();
  }, [currentMessageIndex]);

  return (
    <footer className="container-fluid">
      <div className="banner-container">
        <div className="col-md-1 banner">
          <img
            src="https://i.imgur.com/j9uTn4R.png"
            id="camarito"
            alt="camarito"
          />
        </div>
        <div className="col-md-11 mensajes">
          <p className="message-text">{currentMessage}</p>
        </div>
      </div>
    </footer>
  );
}

export { Footer };
